// var md5 = require('md5');
import md5 from 'md5';

const Authenticate = (function (auth) {

    const KEY_OF_USERDATA = 'cms-user';
    const KEY_OF_USERNAME = 'username';
    const KEY_OF_EMAIL = 'email';
    const KEY_OF_LOGGEDIN = 'logged-in';
    const KEY_OF_TOKEN = 'token';
    const KEY_OF_OLDREPORT_TOKEN = 'oldToken';
    const KEY_OF_REQUIRESALTERPASSWORD = 'require-alterpassword';
    const KEY_OF_PERMISSIONS = 'permissions';

    var userData = null;

    auth.loadUserData = function () {
        var rawdata = localStorage.getItem(KEY_OF_USERDATA);
        userData = JSON.parse(rawdata);
    };

    auth.loggedIn = function () {
        return localStorage.getItem(KEY_OF_LOGGEDIN) == 'true';
    };

    auth.getToken = function () {
        return localStorage.getItem(KEY_OF_TOKEN);
    };

    auth.getOldToken = function () {
        // return localStorage.getItem(KEY_OF_OLDREPORT_TOKEN);
        // return md5("0b237aa640e2e08c44e2a88ddbcd004f");
        var timestamp = + new Date();
        var salt = "0b237aa640e2e08c44e2a88ddbcd004f";
        return {
            token: md5(salt + timestamp),
            timestamp: timestamp
        }
    };

    auth.requiresAlterPassword = function () {
        return localStorage.getItem(KEY_OF_REQUIRESALTERPASSWORD) == 1;
    };

    auth.getUserName = function () {
        auth.loadUserData();
        if (userData && userData.token) {
            return userData.username;
        }
        else {
            return '';
        }
    };

    auth.getEmail = function () {
        return localStorage.getItem(KEY_OF_EMAIL);
    };

    auth.isAdmin = function () {
        auth.loadUserData();
        if (userData && userData.isAdmin) {
            return userData.isAdmin;
        }
        return false;
    };

    auth.hasPermission = function (resource) {
        // if(resource == "permissionManage"){
        //     if(auth.getEmail() =="admin@we.com"){
        //         return true;
        //     }else{
        //         return false;
        //     }
        // }
        if (auth.isAdmin()) {
            return true;
        }

        let rawdata = localStorage.getItem(KEY_OF_PERMISSIONS);
        try {
            let permissions = JSON.parse(rawdata);
            if (permissions) {
                return permissions[resource];
            }
        } catch (error) {
        }
        return false;
    };


    auth.login = function (userData) {
        userData = userData || {};
        if (userData.token) {
            localStorage.setItem(KEY_OF_USERNAME, userData.username);
            localStorage.setItem(KEY_OF_EMAIL, userData.email);
            localStorage.setItem(KEY_OF_TOKEN, userData.token);
            localStorage.setItem(KEY_OF_LOGGEDIN, true);
            localStorage.setItem(KEY_OF_REQUIRESALTERPASSWORD, userData.isReset);
            localStorage.setItem(KEY_OF_USERDATA, JSON.stringify(userData));
            if (userData.permissions) {
                let permissions = {};
                userData.permissions.forEach((item) => {
                    permissions[item.index] = item;
                });
                localStorage.setItem(KEY_OF_PERMISSIONS, JSON.stringify(permissions));
            }
            return true;
        }
        return false;
    };
    auth.loginOld = function (oldUserData) {
        var oldUserData = oldUserData || {};
        if (oldUserData.token) {
            localStorage.setItem(KEY_OF_OLDREPORT_TOKEN, oldUserData.token);
            return true;
        }
        return false;
    };

    auth.setRequiresAlterPassword = function (requiresAlter) {
        localStorage.setItem(KEY_OF_REQUIRESALTERPASSWORD, requiresAlter);
    };

    auth.setAsLoggedOut = function () {
        localStorage.setItem(KEY_OF_LOGGEDIN, false);
    };

    auth.logout = function () {
        // localStorage.clear();
        localStorage.removeItem(KEY_OF_USERDATA);
        localStorage.removeItem(KEY_OF_USERNAME);
        localStorage.removeItem(KEY_OF_EMAIL);
        localStorage.removeItem(KEY_OF_TOKEN);
        localStorage.removeItem(KEY_OF_LOGGEDIN);
        localStorage.removeItem(KEY_OF_REQUIRESALTERPASSWORD);
        localStorage.removeItem(KEY_OF_PERMISSIONS);
    };

    return auth;
})(Authenticate || {});

export default Authenticate;

