import Vue from 'vue'
import filter from "./utils/filter";

import VueRouter from 'vue-router'
import { routes } from './config/router.config.js'

import auth from './utils/authenticate'

// 添加mockjs拦截请求，模拟返回服务器数据
// import mockTest from './mock/mockTest'

import ElementUI from 'element-ui'

//引入Echats图表组件
// import ECharts from 'vue-echarts/components/ECharts.vue'


import ECharts from 'vue-echarts' // refers to components/ECharts.vue in webpack

// import ECharts modules manually to reduce bundle size
import 'echarts/lib/chart/bar'
import 'echarts/lib/component/tooltip'

// If you want to use ECharts extensions, just import the extension package and it will work
// Taking ECharts-GL as an example:
// You only need to install the package with `npm install --save echarts-gl` and import it as follows
// import 'echarts-gl'



// register component to use
Vue.component('chart', ECharts)

//引入饿了么的UI组件库的样式
// import 'element-ui/lib/theme-default/index.css'

//2.0element
import "element-ui/lib/theme-chalk/index.css";
import "./assets/style/font-awesome.min.css";
import App from "./App";

import './assets/style/font-awesome.min.css'

Vue.use(ElementUI);
Vue.use(VueRouter);

const router = new VueRouter({
    routes
});

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!auth.loggedIn()) {
            next({
                path: '/login',
                query: { redirect: to.fullPath }
            })
        } else if (auth.requiresAlterPassword()) {
            next({
                path: '/firstLogin'
            });
        } else if (to.meta.permission && !auth.hasPermission(to.meta.permission)) {
            next({
                path: '/admin/404'
            });
        } else {
            next();
        }
    } else {
        next();
    }
});

new Vue({
    el: '#app',
    router,
    filter,
    // render: h=>h(App),
    template: '<App/>',
    components: { App },
});


